<template>
  <div id="login">
    <div class="wrapper">
      <h2 class="title">Audio</h2>
      <p class="description">It's modular and designed to last</p>
      <form class="form-group">
        <div class="input-group">
          <i class="fa-solid fa-envelope"></i>
          <input type="email" placeholder="Email" v-model="email" />
        </div>
        <div class="input-group">
          <i class="fa-solid fa-lock"></i>
          <input
            type="password"
            placeholder="Password"
            v-model="password"
            autocomplete
            @keyup.enter="login"
          />
        </div>
        <div class="error-message">{{ error }}</div>
        <div class="btn" @click="login">
            <span v-if="!loading">Login</span>
            <span v-else>Loging...</span>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { auth } from '@/firebase/config'
import { signInWithEmailAndPassword } from "firebase/auth";

export default {
  name: 'Login',
  data () {
    return {
      email: null,
      password: null,
      error: '',
      loading: false
    }
  },
  computed: {
    isLogin () {
      return this.$store.state.isLogin
    }
  },
  watch: {
    isLogin (val) {
      if (val) {
        console.log('cháº¡y')
        this.$router.push({ name: 'Admin' })
      }
    }
  },
  methods: {
    async login () {
      this.loading = true
      await signInWithEmailAndPassword(auth, this.email, this.password)
        .then((user) => {
          this.error = ''
          this.$store.dispatch('login', user)
          this.$router.push({ name: 'Admin' })
        })
        .catch((error) => {
          this.error = error
        })
        .finally(() => {
          this.loading = false
        })
    }
  }
}
</script>

<style lang="scss" scoped>
#login {
  position: relative;
  background-image: url("../assets/img/login-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 100vh;
}
.wrapper {
  position: absolute;
  max-width: 330px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.title {
  color: $color-white;
  font-size: 52px;
  line-height: 66px;
  font-weight: 700;
  text-align: center;
}
.description {
  margin: 8px 0 60px;
  color: $color-white;
  font-size: 14px;
  line-height: 20px;
  font-weight: 700;
  text-align: center;
}
.form-group {
  .input-group {
    margin-bottom: 24px;
    background-color: $color-white;
    padding: 16px;
    display: flex;
    align-items: center;
    grid-gap: 8px;
    color: $color-grey;
    font-size: 14px;
    line-height: 20px;
    font-weight: 400;
    border-radius: 10px;
    input {
      border: none;
      outline: none;
    }
  }
  .error-message {
    margin-bottom: 8px;
    color: $color-error;
    font-size: 14px;
    text-align: center;
  }
  .btn {
    padding: 16px;
  }
}
</style>